*{
    margin:0;
    padding:0;
     font-family: 'Oswald', sans-serif;
   /*   font-family: 'Quicksand', sans-serif; 
    font-family: 'Raleway', sans-serif;   */ 

}
.first{
    margin: 0 auto;
    width:100%;
    height: 100vh;
    background: #44ad81;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

}
.center{
    position: absolute;
    width:70%;
    text-align: center;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0, 0.5);
    border-radius: 15px;
    overflow: hidden;
    height: auto;
}

.data{
    padding:25px 20px;
    text-align: left;
    background-position:center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    position: relative;
    margin: 20px auto;
    box-shadow: 4px 2px 10px 2px rgba(0,0,0, 0.5);
    border-radius: 5px;
    width:75%;
    height: 50%;
   
    
}
#err{
    color:red;
}
h2 , h3{
    text-transform: capitalize;
}
style{
    visibility: hidden;
}
#btn{
    position: absolute;
    right:10px;
}
.warn{
    z-index: 1;
    position: absolute;
    color: grey;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    box-sizing: border-box;
    border-radius: 4px;
    background-color: white;
    opacity: 0.9;
    width: 100%;
    padding:7px 15px;
    right:0px;
    bottom: 65px;
}
.warn li{
    list-style: none;
    z-index: 1;
}
.help{
    position: absolute;
    bottom:5px;
    right:0px;
}

#header{
    background-color: #44ad81;
    margin-top: 10px;
    box-sizing: border-box;
    box-shadow: 2px 5px 5px 3px rgba(0,0,0, 0.5);
}
.ip{
  width:70%;
}
.locate {
    width: 15%;
    padding:5px 0;
}
.js{
    display: flex;
    flex-direction: row;
    padding: 10px 20px;
}
.fdata{
    padding: 10px 0;
}

#name{ 
    font-size: 25px;
    color:rebeccapurple;
}
#sunrise ,#sunset,#desc,#feels{
    color:grey;
    font-size: 20px;
}

@media (min-width:790px){
    .js{display: flex; align-items: center;justify-content: center;}
    .ip{width:auto; }
    .locate{width: auto; padding:0;}
    .center{ width:50%;}
}